import { HStack, Image, Text } from '@chakra-ui/react'

type PoweredByHaedalProps = {
  color?: string
  fontSize?: string
  imgW?: string
  imgH?: string
  mt?: string
  category: 'Haedal' | 'Cetus'
}
function PoweredByHaedal(props: PoweredByHaedalProps) {
  const { color, imgW, imgH, fontSize, mt = '-4px', category } = props
  return (
    <HStack w="100%" mt={mt}>
      <Text fontSize={fontSize || '12px'} color={color ? color : category == 'Cetus' ? 'primary_gray' : 'text_haedal'} whiteSpace="nowrap">
        Powered by {category}
      </Text>
      <Image
        decoding="async"
        src={category === 'Haedal' ? '/images/haedal-logo@2x.png' : '/images/img-logo@2x.png'}
        // borderRadius="100%"
        w={imgW || 'auto'}
        h={imgH || '12px'}
      />
    </HStack>
  )
}
export default PoweredByHaedal
